#navigation .navbar-secondary .navbar-settings {
    visibility:hidden;
    position:absolute;
    top:100%;
    right:0px;
    width:360px;
    background-color:#FFF;
    padding:0px 20px;
    margin-top:20px;
    opacity:0;
    box-shadow:0px 3px 5px rgba(0,0,0,.1);
    transition:all .2s ease;
}

#navigation .navbar-secondary .navbar-settings .ion{
    font-size: 28px;
}

.ie #navigation .navbar-secondary .navbar-settings {
    border:1px solid #e9e9e9;
    border-top:0px;
}
#navigation .navbar-secondary .navbar-settings.in {
    visibility:visible;
    margin-top:0px;
    opacity:1;
}
    #navigation .navbar-secondary .navbar-settings .settings-heading {
        overflow:hidden;
        color:#303030;
        font-size:15px;
        line-height:22px;
        padding:10px 0px;
        margin-bottom:20px;
        border-bottom:1px solid #e9e9e9;
    }
        #navigation .navbar-secondary .navbar-settings .settings-heading .settings-count {
            float:left;
            margin:0px;
        }
        #navigation .navbar-secondary .navbar-settings .settings-heading .settings-price {
            float:right;
            color:#00d27f;
            font-weight:400;
            margin:0px;
        }
    #navigation .navbar-secondary .navbar-settings .casettingsrt-list {
        padding:0px;
        margin:0px;
    }
        #navigation .navbar-secondary .navbar-settings .settings-list li {
            list-style-type:none;
            overflow:hidden;
            position:relative;
            margin-bottom:20px;
        }
            #navigation .navbar-secondary .navbar-settings .settings-list li .settings-btn-close {
                position:absolute;
                top:0px;
                right:0px;
                color:#a9a9a9;
                font-size:24px;
                line-height:1;
            }
            #navigation .navbar-secondary .navbar-settings .settings-list li .settings-btn-close:hover { color:#00d27f; }
            #navigation .navbar-secondary .navbar-settings .settings-list li img {
                float:left;
                width:25%;
            }
            #navigation .navbar-secondary .navbar-settings .settings-list li .settings-content {
                float:right;
                width:70%;
                font-size:15px;
                line-height:22px;
                padding-top:5px;
            }
                #navigation .navbar-secondary .navbar-settings .settings-list li .settings-content p {
                    color:#a9a9a9;
                    margin-bottom:5px;
                }
                    #navigation .navbar-secondary .navbar-settings .settings-list li .settings-content p span {
                        color:#303030;
                        font-weight:400;
                        margin-left:5px;
                    }
                #navigation .navbar-secondary .navbar-settings .settings-list li .settings-content a {
                    font-family:Montserrat, Arial, sans-serif;
                    font-weight:400;
                }
        #navigation .navbar-secondary .navbar-settings .settings-footer {
            text-align:center;
            padding:15px 0px;
            border-top:1px solid #e9e9e9;
        }
            #navigation .navbar-secondary .navbar-settings .settings-footer .btn-custom {
                font-size:12px;
                padding:11px 22px;
                margin:0px 5px;
            }


            #navigation .navbar-secondary .navbar-settings .settings-footer {
				text-align:center;
				padding:15px 0px;
				border-top:1px solid #e9e9e9;
			}
				#navigation .navbar-secondary .navbar-settings .settings-footer .btn-custom {
					font-size:12px;
					padding:11px 22px;
					margin:0px 5px;
				}


